<template>
	<div class="input_form_wrapper" :class="{ active: isFoucsed, required, icon_left: $slots.icon_left, is_invalid }">
		<div class="icon_left">
			<slot name="icon_left" v-bind="{ active: isFoucsed }"></slot>
		</div>
		<div class="input_control">
			<label :for="id" v-if="label">{{ label }}</label>
			<div class="input_wrapper">
				<input :id="id" class="form_input" v-bind="inputProps" v-model="val" :type="type" :placeholder="placeholder" @focus="focus" @blur="blur" :required="required" />
				<slot name="end_of_input"></slot>
			</div>
			<span class="error_msg" v-if="errorMsg">{{ errorMsg }}</span>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			inputProps: { type: Object },
			id: { type: String },
			label: { type: String },
			value: { type: [String, Number] },
			type: { type: String, default: "text" },
			placeholder: { type: String, default: "" },
			required: { type: Boolean, default: false },
			is_invalid: { type: Boolean, default: false },
			errorMsg: { type: String, default: "" },
		},

		data() {
			return {
				isFoucsed: false,
			};
		},

		computed: {
			val: {
				get() {
					return this.value;
				},
				set(val) {
					this.$emit("input", val);
				},
			},
		},

		methods: {
			onChange(e) {
				this.value = e.target.value;
			},

			focus() {
				this.isFoucsed = true;
				this.$emit("focus");
			},

			blur() {
				this.isFoucsed = false;
				this.$emit("focus");
			},
		},
	};
</script>

<style lang="scss" scoped>
	.input_form_wrapper {
		display: flex;
		width: 100%;
		margin-bottom: 1rem;
		&.icon_left {
			.icon_left {
				width: 36px;
				top: 21px;
				position: relative;
				align-self: flex-start;
				overflow: hidden;
				svg {
					width: 1em;
					height: 1em;
				}
			}
		}
		.input_control {
			width: inherit;
			label {
				display: block;
				font-size: 12px;
				color: #0000009a;
				margin-bottom: 0;
				text-align: left;
			}

			.input_wrapper {
				position: relative;
				display: inline-block;
				width: 100%;

				.form_input {
					width: 100%;
					padding: 3px 0 7px;
					border: none;
					border-bottom: 1px solid #ccc;
					box-sizing: border-box;
					font-size: 16px;
					font-weight: 400;
					color: #000000ef;
					background-color: #fff;
					transition: all 0.3s ease;
					outline: none;
					&:hover,
					&:focus {
						border-color: transparent;
					}
				}

				&:before {
					content: "";
					position: absolute;
					bottom: 0;
					width: 0;
					left: 50%;
					height: 2px;
					transition: 0.2s ease-out;
					background-color: #000000;
				}
			}

			.error_msg {
				color: var(--danger);
				font-size: 12px;
				display: block;
				text-align: left;
				margin-top: 3px;
			}
		}

		&.required {
			.input_control {
				label {
					&:after {
						content: "*";
						margin-left: 4px;
					}
				}
			}
		}

		& {
			.input_wrapper {
				&:hover {
					&:before {
						left: 0;
						width: 100%;
					}
					.form_input {
						border-color: transparent;
					}
				}
			}
			&.active,
			&[vuelidate-invalid="true"],
			&.is_invalid {
				.input_wrapper {
					&:before {
						left: 0;
						width: 100%;
						background-color: var(--primary);
					}
					.form_input {
						border-color: transparent;
					}
				}
			}
		}

		&.active {
			.icon_left {
				color: var(--primary);
			}
			.input_control {
				label {
					color: var(--primary);
				}
			}
		}

		&[vuelidate-invalid="true"],
		&.is_invalid {
			.icon_left {
				color: var(--danger);
			}
			.input_control {
				label {
					color: var(--danger);
				}
				.input_wrapper {
					&:before {
						background-color: var(--danger);
					}
				}
			}
		}
	}
</style>
