<template>
	<div class="account create_one mt-5 position-relative">
		<b-container>
			<div class="header mt-4">
				<div class="h3 mb-0">{{ $t("editIntro") }}</div>
			</div>
			<b-form @submit.prevent="save">
				<b-row class="mt-5">
					<b-col cols="12" md="9">
						<!-- Work -->
						<section class="mb-5">
							<div class="field_title pt-1 mb-4">
								<div class="title font-weight-500">{{ $t("work") }}</div>
							</div>
							<div class="mb-4 pb-1">
								<form-input :label="$t('company')" v-model="intro.company" :placeholder="$t('companyPlaceholder')">
									<span slot="icon_left">🏢</span>
								</form-input>
							</div>
							<div class="mb-4">
								<form-input :label="$t('position')" v-model="intro.position" :placeholder="$t('positionPlaceholder')">
									<span slot="icon_left">💼</span>
								</form-input>
							</div>
						</section>

						<!-- Personal -->
						<section class="mb-5">
							<div class="field_title pt-1 mb-4">
								<div class="title font-weight-500">{{ $t("personal") }}</div>
							</div>
							<div class="mb-4 pb-1">
								<form-input :label="$t('currentCity')" v-model="intro.currentCity" :placeholder="$t('currentCityPlaceholder')">
									<span slot="icon_left">📍</span>
								</form-input>
							</div>
							<div class="mb-4">
								<form-input :label="$t('bio')" v-model="intro.bio" :placeholder="$t('bioPlaceholder')">
									<span slot="icon_left">💬</span>
								</form-input>
							</div>
						</section>

						<!-- Links -->
						<section class="mb-5">
							<div class="field_title pt-1 mb-4">
								<div class="title font-weight-500">{{ $t("links") }}</div>
							</div>
							<div class="mb-4 pb-1">
								<form-input :label="$t('linkedIn')" v-model="intro.linkedIn" :placeholder="$t('linkedInPlaceholder')">
									<span slot="icon_left">
										<b-img :src="require('../assets/media/linkedin.png')" style="width: 1rem; height: 1ren"></b-img>
									</span>
								</form-input>
							</div>
							<div class="mb-4">
								<form-input :label="$t('twitter')" v-model="intro.twitter" :placeholder="$t('twitterPlaceholder')">
									<span slot="icon_left">
										<b-img :src="require('../assets/media/twitter.png')" style="width: 1rem; height: 1ren"></b-img>
									</span>
								</form-input>
							</div>
						</section>
					</b-col>
				</b-row>
			</b-form>
		</b-container>
		<div class="footer_control bg-white border-top w-100">
			<b-container class="h-100">
				<div class="d-flex justify-content-end align-items-center h-100">
					<b-overlay :show="isLoading" rounded opacity="0.6" spinner-small spinner-variant="primary" class="d-inline-block">
						<b-button variant="primary" @click="save" class="px-3" :disabled="isLoading"> {{ $t("save") }} </b-button>
					</b-overlay>
				</div>
			</b-container>
		</div>
	</div>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";

	export default {
		components: { FormInput },

		data() {
			return {
				intro: {
					company: "",
					position: "",
					currentCity: "",
					bio: "",
					linkedin: "",
					twitter: "",
				},
				isLoading: false,
			};
		},

		async mounted() {
			await this.$store.dispatch("getMe");
			this.intro = { ...this.$me.intro };
		},

		methods: {
			async save() {
				this.isLoading = true;

				try {
					await this.$store.dispatch("updateMe", { intro: this.intro });
				} finally {
					this.isLoading = false;
				}
			},
		},
	};
</script>
